import { InputProps } from "@/components/Info/InfoProps";
import axios from "axios";
import store from ".";
import NetConfig from "@/config/net.cfg";
import Qs from "qs"
import { getRoomIdProps } from "../../../express/src/views/InputProps";

const BASE_URL = NetConfig.postUrl;


// @ts-ignore
export let socket: WebSocket = null

const config = {
  method: "post",
  timeout: 10000,
  headers: {
    "Content-Type": "application/x-www-form-urlencoded",
    // "Content-Type": 'application/x-www-form-urlencoded'
  },
};

const myAxios = axios.create(config);

// 默认 post 请求，使用 application/json 形式
// myAxios.defaults.headers.post["Content-Type"] = "application/json";

export const checkIsMail = (s0: string) => {
  return (
    s0.search(
      "^[a-zA-Z0-9][a-zA-Z0-9_]+@[a-zA-Z0-9]+.[a-zA-Z]{2,5}(.[a-zA-Z]{2,5})*$"
    ) !== -1
  );
};

export const checkIsUsername = (s0: string) => {
  if (s0.length !== 0 && s0.indexOf("\\") === -1) {
    return true;
  }
  return false;
};

export const checkIsPassword = (s0: string) => {
  if (s0.indexOf("\\") === -1) {
    return true;
  }
  return false;
};

export default myAxios;


export const getMailCode = (data: InputProps.getMailCodeProps) => {
  return axios({
    ...config,
    url: BASE_URL + "getMailCode",
    data: Qs.stringify(data),
  });
}

