import { createStore } from 'vuex'
import * as action from "./actions"
import { getCookie } from './cookie';
import * as AXIOS from './axios';

const store = createStore({
  state: {
    lastRouter: "/index/",
    nowRouter: "",
    indexTopAlpha: 1,
    indexSearchKw: "",
    indexSearchReturn: false,
    indexSearchType: "",
    indexSearchTime: (new Date()).getTime(),

    nowBattle: {},
    battlePoint: 0,

    cookie: {
      readed: false,
      logined: false,
      token: ""
    },

    userinfo: {
      token: null,
      email: '',
      username: '',
      headerType: '',
      ranks: []
    }
  },
  getters: {
  },
  mutations: {
    [action.SET_NOW_ROUTER] (state, peyload) {
      state.nowRouter = peyload.nowRouter
    },
    [action.SET_TOPMENU_ALPHA] (state, peyload) {
      state.indexTopAlpha = peyload;
    },
    [action.SET_SEARCH_KW] (state, peyload) {
      state.indexSearchKw = peyload
    },
    [action.SET_SEARCH_TYPE] (state, peyload) {
      state.indexSearchType = peyload
    },
    [action.SET_BATTLE] (state, battle){
      state.nowBattle = battle;
    },
    [action.SET_BATTLE_POINT] (state, point) {
      state.battlePoint = point
    },
    [action.READ_COOKIE] (state){
      if(getCookie("token")!==null){
        state.cookie.token = getCookie("token") as string;
        state.cookie.logined = true
      }else{
        state.cookie.logined = false
      }
      state.cookie.readed = true
    },
    [action.SET_LAST_ROUTER] (state, url){
      state.lastRouter = url
    },
    [action.SET_USER_INFO] (state, data){
      state.userinfo = {...state.userinfo, ...data}
    },
    [action.SET_SEARCH_RETURN] ( state) {
      state.indexSearchReturn = !state.indexSearchReturn;
    },
    [action.SET_SEARCH_TIME] ( state, time: number) {
      state.indexSearchTime = time;
    }
  },
  actions: {
    [action.PULL_USER_INFO] ({ commit, state }) {
      if(state.userinfo.token!==null){
        return
      }
      let token = getCookie('token')

      if(token!==null){
        AXIOS.getUserInfo({token}).then(res=>{
          // if(res.data['status']===0){
            commit(action.SET_USER_INFO,  {...res.data['data'], ranks: res.data['ranks'], token})
          // }
        })
      }

    }
  },
  modules: {
  }
})

export default store
